<template>
    <Navbar/>
    <header>
      <div class="settings-box">
        <div class="left-section">
          <div class="profile-settings-header">
            <div class="change-pfp">
              <input type="file" id="display-pfp" @change="changeImage($event, 'pfp')" accept="image/*">
              <label for="display-pfp">
                <i class="fa fa-file-image"></i>
              </label>
              <img :src="previewpfp ? previewpfp : currentUser.profile?.profilePicture" alt="pfp">
            </div>
            <h3 class="username-roles notranslate">{{ currentUser.profile?.username }}</h3>
          </div>
          <div class="buttons">
            <button @click="showPage(0)">Fiókom</button>
            <button @click="showPage(1)">Megjelenés</button>
            <button @click="showPage(2)">Értesítések</button>
            <button @click="showPage(3)">Napló</button>
            <button @click="showPage(4)">Biztonság</button>
          </div>
        </div>
        <div class="right-section">
          <div class="section account">
            <h2>Fiók beállítások</h2>
            <form @submit.prevent="saveUser(currentUser._id, 'profile')" v-if="currentUser.profile?.username" class="change form">
                <div class="change-row">
                    <input type="text" v-model="currentUser.profile.username" class="change-input" id="ch-us" minlength="3" maxlength="20" required>
                    <label for="ch-us" class="change-label">Felhasználónév</label>
                </div>
                <div class="change-row">
                    <input type="text" v-model="currentUser.profile.name" class="change-input" id="ch-na" minlength="4" maxlength="20" required>
                    <label for="ch-na" class="change-label">Teljes név</label>
                </div>
                <div class="change-row">
                    <input type="email" v-model="currentUser.profile.email" class="change-input" id="ch-em" minlength="6" maxlength="30" required>
                    <label for="ch-em" class="change-label">Email cím</label>
                </div>
                <div class="change-row">
                    <input type="text" v-model="currentUser.profile.biography" class="change-input" id="ch-bg" minlength="4" maxlength="30" required>
                    <label for="ch-bg" class="change-label">Biográfia</label>
                </div>
                <div class="change-row">
                    <input type="password" v-model="password" class="change-input" id="ch-pw" minlength="6">
                    <label for="ch-pw" class="change-label">Jelszó</label>
                </div>
                <div class="change-row">
                    <button class="change-button" type="submit">Mentés</button>
                </div>
            </form>
          </div>
          <div class="section appearance">
            <h2>Megjelenés</h2>
            <div class="bg-image">
              <h3>Háttérkép</h3>
              <div class="upload-img">
                <input type="file" id="upload-img" @change="changeImage($event, 'bgimg')" accept="image/*">
                <i class="fa fa-trash" @click="saveUser(currentUser._id, 'appearance', 'deletebgimg')"></i>
                <label for="upload-img">
                  <i class="fa fa-file-image"></i>
                </label>
                <img :src="previewbg ? previewbg : bgimage" alt="bg-image">
              </div>
              <button type="submit" @click="saveUser(currentUser._id, 'appearance', 'theme')" class="submit-btn">
                Mentés
              </button>
            </div>
          </div>
          <div class="section notifications">
            <h2><i class="fa fa-refresh" @click="this.getCurrentUser"></i> Értesítések kezelése <i class="fa fa-trash" @click="removeNotifications(currentUser._id)"></i></h2>
            <div class="section-notifications">
              <div class="notification" v-for="notification in currentUser.profile?.notificationList" :key="notification.id" v-bind:title="formatDate(notification.createdAt)">
                <h2><img :src="notification.profilePicture" alt="pfp"> {{ notification.title }}</h2>
                <h3>{{ notification.message }}</h3>
              </div>
            </div>
          </div>
          <div class="section logs">
            <h2>Bejelentkezési napló</h2>
            <div class="ip-list">
              <div class="ip" v-for="ip in currentUser.profile?.ipList" :key="ip.id">
                <h4>{{ ip.ip }} - {{ formatDate(ip.loggedAt) }}</h4>
              </div>
            </div>
          </div>
          <div class="section security">
            <h2>Biztonsági beállítások</h2>
            <div class="privacy-settings" v-if="currentUser.privacy">
              <h3>A profilomon látszódjon a Teljes nevem</h3>
              <label class="switch name">
                <input type="checkbox" v-model="currentUser.privacy.showName" @click="saveUser(currentUser._id,
                'privacy', boolean = { showName: currentUser.privacy.showName = !currentUser.privacy.showName })">
                <span class="slider round"></span>
              </label>
              <h3>A profilomon látszódjon az Email címem</h3>
              <label class="switch email">
                <input type="checkbox" v-model="currentUser.privacy.showEmail" @click="saveUser(currentUser._id,
                'privacy', boolean = { showEmail: currentUser.privacy.showEmail = !currentUser.privacy.showEmail })">
                <span class="slider round"></span>
              </label>
            </div>
            <form @submit.prevent="removeUser(currentUser._id)">
              <a @click="removeUserModal(true)" class="modal-opener">Fiók törlése</a>
              <div id="modal">
                <div class="modal-content">
                  <h4>A fiók törlése visszavonhatatlan!</h4>
                  <div class="buttons">
                    <a @click="removeUserModal(false)">Mégsem</a>
                    <button type="submit" class="submit-btn">Törlés</button>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </header>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import { userRoles } from '@/assets/js/functions.js';
import Navbar from '@/components/Navbar';
import '@/assets/scss/profilesettings.scss';
export default {
    name: 'ProfileSettings',
    components: {
        Navbar
    },

    data() {
        return {
            previewpfp: '',
            previewbg: '',
            bgimage: '',
            theme: localStorage.getItem('theme') || 'dark',
            isActiveDark: false,
            isActiveLight: false,
            password: '',
            notifications: this.notifications,
        }
    },

    computed: {
        ...mapGetters(['currentUser', 'userError']),
    },

    methods: {
        ...mapActions(['getCurrentUser', 'patchUser', 'patchUserSite', 'patchUserPrivacy', 'uploadImage', 'deleteUser', 'addNotification', 'deleteNotifications']),

        checkUserRoles() {
            userRoles(this.currentUser.profile.roles);
        },

        formatDate(ipDate) {
            const date = new Date(ipDate);
            function addZero(i) {
                if (i < 10) {i = '0' + i}
                return i;
            }
            const text = `${date.getFullYear()}.${addZero(date.getMonth() + 1)}.${addZero(date.getDate())} ${addZero(date.getHours())}:${addZero(date.getMinutes())}`;
            return text;
        },

        changeImage(e, option) {
            if(option == 'pfp') {
                this.pfp = e.target.files[0];
                this.previewpfp = URL.createObjectURL(this.pfp);

                this.addNotification({
                    message: 'Ez csak egy előnézet, mentsd el a Fiókom menüben a Mentés gombbal.',
                    type: 'information'
                });
            }

            if(option == 'bgimg') {
                this.bgimg = e.target.files[0];
                this.previewbg = URL.createObjectURL(this.bgimg);

                this.addNotification({
                message: 'Ez csak egy előnézet, mentsd el a Mentés gombbal.',
                type: 'information'
                });
            }
        },

        async saveUser(userId, option1, option2) {
            let patchedUserSettings;
            let query;

            switch(option1) {
                case 'profile':
                    query = 'profile';
                    if(this.password.length > 0) {
                        const element = document.getElementById('ch-pw');
                        element.required = true;
                    }
                    if(this.pfp) {
                        const formData = new FormData();
                        formData.append('image', this.pfp);

                        await this.uploadImage(formData).then(res => {
                            this.link = res.data.data.link;
                        });
                    }
                    patchedUserSettings = {
                        username: this.currentUser.profile.username,
                        name: this.currentUser.profile.name,
                        email: this.currentUser.profile.email,
                        biography: this.currentUser.profile.biography,
                        profilePicture: this.link ? this.link : this.currentUser.profile.profilePicture,
                        password: this.password ? this.password : undefined,
                    }
                    break;
                case 'appearance':
                    query = 'appearance';
                    if(this.bgimg) {
                        const formData = new FormData();
                        formData.append('image', this.bgimg);

                        await this.uploadImage(formData).then(res => {
                            this.bglink = res.data.data.link;
                        });
                    }

                    if(option2.showNav == true || option2.showNav == false) {
                        if(option2.showNav == true) {
                            document.querySelector('nav').style.display = 'block';
                        }
                        if(option2.showNav == false) {
                            document.querySelector('nav').style.display = 'none';
                        }
                        patchedUserSettings = {
                            showNav: option2.showNav
                        }
                    }

                    if(option2.showFooter == true || option2.showFooter == false) {
                        if(option2.showFooter == true) {
                            document.querySelector('footer').style.display = 'block';
                        }
                        if(option2.showFooter == false) {
                            document.querySelector('footer').style.display = 'none';
                        }
                        patchedUserSettings = {
                            showFooter: option2.showFooter
                        }
                    }

                    if(option2 == 'theme') {
                        if(this.bglink) {
                            document.body.style.backgroundImage = 'url(' + this.bglink + ')';
                            document.body.style.backgroundRepeat = 'no-repeat';
                            document.body.style.backgroundSize = 'auto';
                            document.body.style.animation = 'none';
                        }

                        patchedUserSettings = {
                            backgroundImage: this.bglink ? this.bglink : 'none',
                            theme: this.theme
                        }
                    }

                    if(option2 == 'deletebgimg') {
                        window.location.reload();

                        patchedUserSettings = {
                            backgroundImage: 'none',
                        }
                    }
                    break;
                case 'privacy':
                    query = 'privacy';
                    patchedUserSettings = {
                        showName: document.querySelector('.switch.name input[type="checkbox"]').checked,
                        showEmail: document.querySelector('.switch.email input[type="checkbox"]').checked
                    }
                    break;
            }

            this.patchUser([userId, query, patchedUserSettings]).then(res => {
                if(res.data.success) {
                    this.addNotification({
                        message: res.data.message,
                        type: 'success'
                    });
                }
            }).catch(() => {
                this.addNotification({
                    message: this.userError,
                    type: 'error'
                });
            });
        },

        checkBgimg() {
            if(this.currentUser.appearance.backgroundImage == 'none') {
                this.bgimage = 'https://hmtienda.es/wp-content/uploads/2020/02/Imagen19-768x393.png';
            } else {
                this.bgimage = this.currentUser.appearance.backgroundImage;
            }
        },

        removeUser(userId) {
            this.deleteUser(userId).then(res => {
                if(res.data.success) {
                    this.$router.push('/');
                    this.addNotification({
                        message: res.data.message,
                        type: 'success'
                    });
                }
            }).catch(() => {
                this.addNotification({
                    message: this.userError,
                    type: 'error'
                });
            });
        },

        removeNotifications(userId) {
            this.deleteNotifications(userId).then(res => {
                if(res.data.success) {
                    this.addNotification({
                        message: res.data.message,
                        type: 'success'
                    });
                    this.currentUser.profile.notificationList = '';
                }
            }).catch(err => {
                this.addNotification({
                    message: err,
                    type: 'error'
                });
            });
        },

        removeUserModal(value) {
            const modal = document.getElementById('modal');

            if(value) {
                modal.style.display = 'block';
            } else {
                modal.style.display = 'none';
            }
        },

        showPage(pageName) {
            const section = document.getElementsByClassName('section');
            for (let i = 0; i < section.length; i++) {
                section[i].style.display = 'none';
            }
            section[pageName].style.display = 'block';
        },
    },

    async mounted() {
        await this.getCurrentUser();
        this.checkUserRoles();
        this.checkBgimg();
    }
}
</script>